import React, {useState} from 'react'
import projects from './api.mjs'


export default function Projects() {
  const [projets, setProjets] = useState(projects);

  return (
    <div className="projects">
      <div className="container">
         <h3 className='heading'><span>3. </span>Things that I've build</h3>
         <ul className="d">
            
         { projets.map((curElem) => {
          const { id, title, image, description, skills, github, url } = curElem;

            return(

              <li className="project" key={`${id}`}>
               <div className="left" >
                     {/* <div className="img" style={{backgroundImage: `url(${image})`}}></div> */}
                     <img className='img' src={image} />
               </div>
               <div className="right">
                     <div className="top">
                        <a to="/" className='name'>{title}</a>
                        <div className='btn-wrapper'>
                            <a className="btn" href={`${url}`}>
                              <span className="iconify" data-icon="akar-icons:link-out"></span>
                            </a>
                            <a className="btn" href={`${github}`}>
                              <span className="iconify" data-icon="akar-icons:github-fill"></span>
                            </a>
                        </div>
                     </div>
                     <div className='box'>
                      <p className='desc'>{description}</p>
                      <ul>
                          {
                            skills.map((li) => {
                              return(
                                <li>{li}</li>
                              )
                            })
                          }
                      </ul>
                     </div>
               </div>
            </li>

            )
         })}
    
          </ul>
      </div>
    </div>
  )
}
