import React from 'react'

export default function Contact() {
  return (
    <div className="contact">
      <div className="container">
        <h3 className="heading"><span>4. </span>Get in touch</h3>
        {/* <h1 className="sub-heading">Contact</h1> */}
        <div className="inner">
          <div>
            <p>
              I'm currently looking for an opportunities, my inbox is always open. Whether your have a question or just want to say hi.
            </p>
          </div>
          <div>
          <button className="btn">Say Hello</button>
          </div>
        </div>
      </div>
    </div>
  )
}
